/* eslint-disable */

import React, { useEffect, useState } from 'react';
import {useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let 박스 = styled.div`
  padding : 20px;
`;


let 제목 = styled.h4`
font-size : 25px;
color : ${ props=> props.색상 }
`;

function DetailContent(props) {

  let [에러숨김, 에러숨김변경] = useState(false);
  let [입력값, 입력값변경] = useState('');
  useEffect(()=>{
  let 타이머 = setTimeout(()=>{ 에러숨김변경(true) },2000);
  },[]);

  let { id } = useParams();
  let 찾은상품 = props.조랭퐁상품.find(function(상품){
    return 상품.id == id
  })  
  // let history = useHistory(); 뒤로가기 기능에 썼음

  return(
        <div className="container">
          <박스>
            <제목 className="red">Product Detail</제목>
          </박스>

        
          {/* <input onChange={(e)=>{ 입력값변경(e.target.value)}} /> */}

          {
            에러숨김===false
            ? <div className="my-alert2">
                <p>재고가 얼마 남지 않았습니다</p>
              </div>
            : null
          }
          <div className="row">
            <div className="col-md-6">
              <img alt="" src={찾은상품.url} width="100%" />
            </div>
            <div className="col-md-6 mt-4">
              <h4 className="pt-5">{찾은상품.title}</h4>
              <p>{찾은상품.content}</p>
              <p>{찾은상품.price}원</p>
              <Info 재고={props.재고}></Info>

              <button className="btn btn-primary" onClick={()=>{props.재고변경([9,11,12])}}>주문하기</button>
            </div>
          </div>
        </div>
  )
};

function Info(props) {
  return (
    <p>재고:{props.재고[0]}</p>
  )
}

export default DetailContent